.hero{
  width: 96%;
  height: 70vh;
  position: relative;
  margin-left: auto;
  margin-right: auto;
}
.each-slide{
  margin-top: 0;
}
.hero-mid{
  width: 100%;
  height: 70vh;
  position: relative;  
}
.hero img{
  width: 96%;
  height: 70vh;
  object-fit: cover;
  
}
img{
  margin-top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  
}
.hero-text{
  position: absolute;
  top: 55%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
}
.hero-text h1{
  font-size: 3rem;
  font-weight: 800;
  background: #ffffff;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.hero-text p {
  font-size: 1.7rem;
  color: #fff;
  padding: 0.4rem 0 2rem 0;
  font-weight: 500;
}
.hero-text .show{
  text-decoration: none;
  background: rgb(41, 174, 186);
  padding: 1rem 2rem;
  border-radius: 6px;
  font-size: 1.1rem;
  font-weight: bolder;
  color: #fff;
}
@media screen and (max-width: 555px) {
.hero-text h1{
  padding: 10px 20px;
}
.hero-text p {
  font-size: 1.1rem;
  padding: 0 0 2rem 0;
}
.hero-text .show{
  padding: .6rem 1.1rem;
  border-radius: 6px;
  font-size: 1rem;
}


}